.galeria {
  padding: 100px 20px;
  background-color: #000;
  color: #fff;
  text-align: center;

  P {
    font-size: 2rem;
    margin-bottom: 2rem;
    font-family: 'Dancing Script' o 'Parisienne';
    color: #fff;
  }

  .galeria-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    max-width: 1000px;
    margin: 0 auto;
  }

  .galeria-item {
    position: relative;
    overflow: hidden;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    opacity: 0;
    transform: translateY(50px);

    img {
      width: 100%;
      height: auto;
      display: block;
      transition: transform 0.5s ease;
    }

    .galeria-caption {
      position: absolute;
      bottom: 0;
      width: 100%;
      background: rgba(0, 0, 0, 0.5);
      color: #fff;
      font-size: 1rem;
      padding: 10px;
      opacity: 0;
      transition: opacity 0.5s ease;
      transform: scale(1.05);
      opacity: 1;
    }

    &:hover img {
      transform: scale(1.10);
    }
  }
}
